    font-size: .9375rem;\n            line-height: 36px;\n        }\n    }\n}\n\n.purchase-popup {\n    width: 100%;\n    z-index: 9999;\n    background: #fff;\n    padding: 15px;\n    display: flex;\n    margin-top: 1rem;\n    margin-bottom: 1rem;\n    @include media-breakpoint-down(md) {\n        padding: 15px 20px;\n        display: block;\n    }\n    .btn {\n        margin-right: 20px;\n        font-weight: 500;\n        &.purchase-button {\n            color: #fff;\n            border: none;\n            line-height: 1;\n            vertical-align: middle;\n            min-height: auto;\n        }\n    }\n    p {\n        margin-bottom: auto;\n        margin-top: auto;\n        color: #1c273c;\n        font-weight: 400;\n        vertical-align: middle;\n        line-height: 1;\n        @include media-breakpoint-down(md) {\n            margin-bottom: 10px;\n        }\n    }\n    i {\n        vertical-align: middle;\n        line-height: 1;\n        margin: auto 0;\n        color: darken(#e4e4e4, 20%);\n    }\n}\n\n.removeProbanner {\n    #proBanner {\n        display: none;\n    }\n}","/* Utilities */\n\n.grid-margin {\n  margin-bottom: $card-spacing-y;\n}\n.grid-margin-sm-0 {\n  @media (min-width: 576px) {\n    margin-bottom: 0;\n  }\n}\n.grid-margin-md-0 {\n  @media (min-width: 768px) {\n    margin-bottom: 0;\n  }\n}\n.grid-margin-lg-0 {\n  @media (min-width: 992px) {\n    margin-bottom: 0;\n  }\n}\n.grid-margin-xl-0 {\n  @media (min-width: 1200px) {\n    margin-bottom: 0;\n  }\n}\n.img-lg {\n  width: 92px;\n  height: 92px;\n}\n.img-sm {\n  width: 43px;\n  height: 43px;\n}\n.img-xs {\n  width: 37px;\n  height: 37px;\n}\n.img-ss {\n  width: 26px;\n  height: 26px;\n}\n.stretch-card {\n  @include display-flex;\n  @include align-items(stretch);\n  @include justify-content(stretch);\n  >.card{\n    width: 100%;\n    min-width: 100%;\n  }\n}\n\n.border-right-sm {\n  @media (min-width: 576px) {\n    border-right: $border-width solid $border-color;\n  }\n}\n.border-right-md {\n  @media (min-width: 768px) {\n    border-right: $border-width solid $border-color;\n  }\n}\n.border-right-lg {\n  @media (min-width: 992px) {\n    border-right: $border-width solid $border-color;\n  }\n}\n\n.border-left-sm {\n  @media (min-width: 576px) {\n    border-left: $border-width solid $border-color;\n  }\n}\n.border-left-md {\n  @media (min-width: 768px) {\n    border-left: $border-width solid $border-color;\n  }\n}\n.border-left-lg {\n  @media (min-width: 992px) {\n    border-left: $border-width solid $border-color;\n  }\n}\n\n.text-gray {\n  color: #8c8c8c;\n}\n\n.text-black {\n  color: $black;\n}\n\n.text-small {\n  font-size: 12px;\n}\n\n.flex-grow {\n  flex-grow: 1;\n}\n\n.font-weight-light {\n  font-family: $type1;\n  font-weight: $font-weight-light;\n}\n\n.font-weight-medium {\n  font-family: $type1;\n  font-weight: $font-weight-medium;\n}\n\n.font-weight-semibold {\n  font-family: $type1;\n  font-weight: $font-weight-semibold;\n}\n\n.font-weight-normal {\n  font-family: $type1;\n}\n","/* Demo Styles */\n\n// Add spacing to Boostrap components for demo purpose\n.template-demo {\n    >.btn {\n        @extend .mt-3;\n        @extend .mr-3;\n    }\n    .slider-wrap {\n        height: 100px;\n    }\n    >.btn-toolbar {\n        @extend .mt-3;\n        @extend .mr-3;\n    }\n    >.btn-group,\n    >.btn-group-vertical {\n        @extend .mt-2;\n        @extend .mr-2;\n    }\n    .progress {\n        margin-top: 1.5rem;\n    }\n    .circle-progress {\n        @extend .mt-2;\n    }\n    >h2,\n    >h3,\n    >h4,\n    >h5,\n    >h6,\n    >h1 {\n        border-top: 1px solid $border-color;\n        padding: 0.5rem 0 0;\n    }\n    .ul-slider {\n        .noUi-horizontal {\n            margin-top: 2rem;\n        }\n        .noUi-vertical {\n            margin-right: 2rem;\n        }\n    }\n    nav {\n        .breadcrumb {\n            margin-bottom: 1.375rem;\n        }\n        &:last-child {\n            .breadcrumb {\n                margin-bottom: 0;\n            }\n        }\n    }\n    .editable-form {\n        >.form-group {\n            border-bottom: 1px solid $border-color;\n            padding-bottom: 0.8rem;\n            margin-bottom: 0.8rem;\n        }\n    }\n    .circle-progress {\n        padding: 15px;\n    }\n    .circle-progress-block {\n        @extend .mb-3;\n        @extend .px-2;\n    }\n}\n\n.demo-modal {\n    position: static;\n    display: block;\n    height: auto;\n    .modal-dialog {\n        @extend .mt-0;\n        &.modal-lg {\n            max-width: 100%;\n        }\n    }\n}\n\n.loader-demo-box {\n    @extend .border;\n    @extend .border-secondary;\n    @extend .rounded;\n    width: 100%;\n    height: 200px;\n    @extend .d-flex;\n    @extend .align-items-center;\n}\n\n.dropdown-menu-static-demo {\n    height: 250px;\n    margin-bottom: 20px;\n}",".main-spinner-wrapper {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  height: 100vh;\n  background: #eee;\n  .donut {\n    width: 4rem;\n    height: 4rem;\n    border-radius: 50%;\n    border: 0.3rem solid rgba(theme-color(success), 0.3);\n    border-top-color: theme-color(success);\n    animation: 1.5s spin infinite linear;\n  }\n}\n\n@keyframes spin {\n  to {\n    transform: rotate(360deg);\n  }\n}",".modal-footer {\n    flex-wrap: wrap;\n\n    & > button {\n        margin: 0.25rem;\n    }\n}","/* Forms */\n\n.form-group {\n  margin-bottom: 1.5rem;\n}\n\n.input-group-append,\n.input-group-prepend {\n  color: $input-placeholder-color;\n  width: auto;\n  border: none;\n  .input-group-text {\n    border-color: $border-color;\n    padding: 0.575rem 0.75rem;\n    color: $input-placeholder-color;\n  }\n  button {\n    height: 33.5px;\n    padding-top: .3rem;\n    padding-bottom: .3rem;\n  }\n}\n\n.custom-control {\n  .custom-control-label {\n    line-height: 1.6;\n    margin-bottom: 0;\n  }\n}\n\n.form-control {\n  border: 1px solid $border-color;\n  font-family: $type1;\n  font-size: $input-font-size;\n}\n\nselect {\n  &.form-control {\n    padding: .4375rem .75rem;\n    border: 0;\n    outline: 1px solid $border-color;\n    color: $input-placeholder-color;\n    &:focus {\n      outline: 1px solid $border-color;\n    }\n    @each $color,\n    $value in $theme-colors {\n      &.border-#{$color} {\n        outline: 1px solid $value;\n        &:focus {\n          outline: 1px solid $value;\n        }\n      }\n    }\n  }\n}\n\n.form-group {\n  label {\n    font-size: $default-font-size;\n    line-height: 1;\n    vertical-align: top;\n    margin-bottom: .5rem;\n  }\n  &.has-danger {\n    .form-control {\n      border-color: theme-color(danger);\n    }\n  }\n  .file-upload-default {\n    visibility: hidden;\n    position: absolute;\n  }\n  .file-upload-info {\n    background: transparent;\n  }\n}\n","/* Checkboxes and Radios */\n\n.form-check {\n  position: relative;\n  display: block;\n  margin-top: 10px;\n  margin-bottom: 10px;\n  padding-left: 0;\n\n  .form-check-label {\n    min-height: 18px;\n    display: block;\n    margin-left: 1.75rem;\n    font-size: $default-font-size;\n    line-height: 1.5;\n    .rtl & {\n      margin-left: 0;\n      margin-right: 1.75rem;\n    }\n\n    input {\n      position: absolute;\n      top: 0;\n      left: 0;\n      .rtl & {\n        left: auto;\n        right: 0;\n      }\n      margin-left: 0;\n      margin-top: 0;\n      z-index: 1;\n      cursor: pointer;\n      opacity: 0;\n      filter: alpha(opacity=0);\n    }\n    input[type=\"checkbox\"] {\n      + .input-helper {\n        &:before,\n        &:after {\n          position: absolute;\n          top: 0;\n          left: 0;\n          .rtl & {\n            left: auto;\n            right: 0;\n          }\n        }\n        &:before {\n          content: \"\";\n          width: 18px;\n          height: 18px;\n          border-radius: 2px;\n          border: solid theme-color(primary);\n          border-width: 2px;\n          @include transition(all);\n          transition-duration: 0s;\n          -webkit-transition-duration: 250ms;\n          transition-duration: 250ms;\n        }\n  \n        &:after {\n          @include transition(all);\n          transition-duration: 0s;\n          -webkit-transition-duration: 250ms;\n          transition-duration: 250ms;\n          opacity: 0;\n          filter: alpha(opacity=0);\n          content: \"\";\n          height: 12px;\n          width: 5px;\n          -webkit-transform: rotate(45deg);\n          transform: rotate(45deg);\n          border-style: solid;\n          border-width: 2px;\n          border-color: transparent $white $white transparent;\n          left: 7px;\n          top: 1px;\n          color: $white;\n        }\n      }\n      &:checked {\n        +.input-helper {\n          &:before {\n            background: theme-color(primary);\n            border-width: 0;\n          }\n          &:after {\n            opacity: 1;\n          }\n        }\n      }\n\n      &:disabled {\n        + .input-helper {\n          &:before {\n            border-color: $border-color;\n          }\n        }\n\n        &:checked {\n          + .input-helper {\n            &:before {\n              background-color: $border-color;\n            }\n            &:after {\n              border-color: transparent $white $white transparent;\n            }\n          }\n        }\n      }\n    }\n    input[type=\"radio\"] {\n      +.input-helper {\n        &:before {\n          position: absolute;\n          content: \"\";\n          top: 0;\n          left: 0;\n          .rtl & {\n            left: auto;\n            right: 0;\n          }\n          border: solid theme-color(primary);\n          border-width: 2px;\n          width: 20px;\n          height: 20px;\n          border-radius: 50%;\n          @include transition(all);\n          transition-duration: 0s;\n          -webkit-transition-duration: 250ms;\n          transition-duration: 250ms;\n        }\n\n        &:after {\n          content: \"\";\n          width: 8px;\n          height: 8px;\n          background: $white;\n          border-radius: 50%;\n          top: 6px;\n          left: 6px;\n          .rtl & {\n            left: auto;\n            right: 6px;\n          }\n          -webkit-transition: all;\n          -o-transition: all;\n          transition: all;\n          transition-duration: 0s;\n          -webkit-transition-duration: 250ms;\n          transition-duration: 250ms;\n          opacity: 0;\n          filter: alpha(opacity=0);\n          -webkit-transform: scale(0);\n          -ms-transform: scale(0);\n          -o-transform: scale(0);\n          transform: scale(0);\n          position: absolute;\n        }\n      }\n\n      &:checked {\n        +.input-helper {\n          &:before {\n            background: theme-color(primary);\n            border-width: 0;\n          }\n\n          &:after {\n            opacity: 1;\n            line-height: 1.5;\n            filter: alpha(opacity=100);\n            -webkit-transform: scale(1);\n            -ms-transform: scale(1);\n            -o-transform: scale(1);\n            transform: scale(1);\n          }\n        }\n      }\n\n      &:disabled {\n        + .input-helper {\n          &:before {\n            border-color: $border-color;\n          }\n        }\n\n        &:checked {\n          + .input-helper {\n            &:before {\n              background: $border-color;\n            }\n\n            &:after {\n              background: $white;\n            }\n          }\n        }\n      }\n    }\n  }\n}\n\n@each $color, $value in $theme-colors {\n  .form-check-#{$color} {\n    &.form-check {\n      label {\n        input[type=\"checkbox\"],\n        input[type=\"radio\"] {\n          +.input-helper {\n            &:before {\n              border-color: theme-color($color);\n            }\n          }\n          &:checked {\n            +.input-helper {\n              &:before {\n                background: $value;\n              }\n            }\n          }\n        }\n      }\n    }\n  }\n}\n\n@media (min-width: 576px) {\n  .form-inline .form-check {\n\n  }\n}","/* Icons */\n\n.icons-list {\n  border-left: 1px solid $border-color;\n  border-bottom: 1px solid $border-color;\n  >div {\n    background: $white;\n    border-top: 1px solid $border-color;\n    border-right: 1px solid $border-color;\n    @include display-flex;\n    @include align-items(center);\n    padding:15px 15px;\n    font-family: $type1;\n    font-size: $default-font-size;\n\n    i {\n      display: inline-block;\n      font-size: 20px;\n      width: 40px;\n      text-align: left;\n      color: theme-color(primary);\n    }\n  }\n}\n","/* Tables */\n\n.table {\n    tbody {\n        tr,\n        td {\n            border-bottom-width: 5px;\n        }\n    }\n    margin-bottom: 5px;\n    thead {\n        border-radius: 4px;\n        th {\n            border-top: 0;\n            font-family: $type1;\n            font-weight: $font-weight-medium;\n            font-weight: initial;\n            i {\n                margin-left: 0.325rem;\n            }\n        }\n    }\n    th,\n    td {\n        vertical-align: middle;\n        border-bottom-width: 5px;\n        font-size: $default-font-size;\n        line-height: 1;\n        img {\n            width: 36px;\n            height: 36px;\n            border-radius: 100%;\n        }\n        .badge {\n            margin-bottom: 0;\n        }\n    }\n    &.table-borderless {\n        border: none;\n        tr,\n        td,\n        th {\n            border: none;\n        }\n    }\n}","/* Buttons */\n\n.btn {\n  font-size: $btn-font-size;\n  line-height: 1;\n  font-family: $type1;\n  font-weight: $font-weight-semibold;\n  margin-right: 3px;\n  i {\n    font-size: 1rem;\n  }\n  &.btn-rounded {\n    @include border-radius(50px);\n  }\n  &.btn-fw {\n    min-width: $button-fixed-width;\n  }\n  &.btn-sm {\n    font-size: $btn-font-size-sm;\n  }\n  &.btn-lg {\n    font-size: $btn-font-size-lg;\n  }\n  &.btn-xs {\n    padding: $btn-padding-y-xs $btn-padding-x-xs;\n    font-size: $btn-font-size-xs;\n  }\n  /* Buttons with only icons */\n  &.btn-icon {\n    width: 42px;\n    height: 42px;\n    padding: 0;\n  }\n  /* Buttons with icon and text */\n  &.btn-icon-text {\n    .btn-icon-prepend {\n      margin-right: .5rem;\n    }\n    .btn-icon-append {\n      margin-left: .5rem;\n    }\n  }\n  &.btn-social-icon {\n    width: 50px;\n    height: 50px;\n    padding: 0;\n  }\n  &.btn-no-hover-bg {\n    &:hover{\n      background: transparent;\n      color: currentColor;\n    }\n  }\n}\n\n.btn-group {\n  .btn {\n    margin-right: 0;\n    + .btn {\n      border-left: 0;\n    }\n  }\n}\n\n.btn-toolbar {\n  .btn-group {\n    +.btn-group {\n      @extend .ml-2;\n    }\n  }\n}\n/*social buttons*/\n@each $color, $value in $social-colors {\n  .btn-#{$color} {\n    @include social-button(social-color($color));\n  }\n  .btn-outline-#{$color} {\n    @include social-outline-button(social-color($color));\n  }\n}\n/* inverse buttons */\n@each $color, $value in $theme-colors {\n  .btn-inverse-#{$color} {\n    @include button-inverse-variant($value);\n  }\n  .btn-#{$color}:not(.btn-light) {\n    color: $white;\n    &:hover,\n    &:focus,\n    &:active {\n      color: $white;\n    }\n    &:focus,\n    &:active {\n      background: $value;\n      border-color: $value;\n    }\n  }\n  .btn-outline-#{$color} {\n    &:hover,\n    &:focus,\n    &:active {\n      background: theme-color($color);\n      color: $white;\n    }\n  }\n}","@mixin social-button($color) {\n  background: $color;\n  color: $white;\n\n  &:hover,\n  &:focus {\n    background: darken($color, 10%);\n    color: $white;\n  }\n  &.btn-social-icon-text {\n    padding: 0 1.5rem 0 0;\n    background: lighten($color, 10%);\n    i {\n      background: $color;\n      padding: .75rem;\n      display: inline-block;\n      margin-right: 1.5rem;\n    }\n  }\n}\n@mixin social-outline-button($color) {\nborder: 1px solid $color;\ncolor: $color;\n&:hover {\n  background: $color;\n  color: $white;\n}\n}\n@mixin button-inverse-variant($color, $color-hover: $white) {\n  background-color: rgba($color, 0.2);\n  background-image: none;\n  border-color: rgba($color, 0);\n  &:not(.btn-inverse-light) {\n    color: $color;      \n  }\n  @include hover {\n    color: $color-hover;\n    background-color: $color;\n    border-color: $color;\n  }\n\n  &.focus,\n  &:focus {\n    box-shadow: 0 0 0 3px rgba($color, .5);\n  }\n\n  &.disabled,\n  &:disabled {\n    color: $color;\n    background-color: transparent;\n  }\n\n  &.active,\n  &:active,\n  .show > &.dropdown-toggle {\n    color: $color-hover;\n    background-color: $color;\n    border-color: $color;\n  }\n}\n","/* Breadcrumbs */\n\n.breadcrumb {\n  border: $border-width solid $border-color;\n\n  .breadcrumb-item {\n    font-size: $breadcrumb-font-size;\n  }\n\n  &.breadcrumb-custom {\n    padding: $breadcrumb-custom-padding-y $breadcrumb-custom-padding-x;\n    border-color: $breadcrumb-item-bg;\n    .breadcrumb-item {\n      font-size: $breadcrumb-font-size;\n      background: $breadcrumb-item-bg;\n      padding: $breadcrumb-custom-item-padding-y $breadcrumb-custom-item-padding-x;\n      color: $breadcrumb-custom-item-color;\n      display: inline-block;\n      vertical-align: top;\n      &:last-child {\n        background: transparent;\n      }\n      &:before{\n        content: \"\";\n      }\n      a{\n        position: relative;\n        color: inherit;\n        border: 1px solid $breadcrumb-item-bg;\n        display: inline-block;\n        vertical-align: top;\n        &:before,&:after{\n          position: absolute;\n          top: -9px;\n          width: 0;\n          height: 0;\n          content: \"\";\n          border-top: 21px solid transparent;\n          border-bottom: 21px solid transparent;\n        }\n        &:before{\n          right: -22px;\n          z-index: 3;\n          border-left-color: $breadcrumb-item-bg;\n          border-left-style: solid;\n          border-left-width: 12px;\n        }\n        &:after{\n          border-top: 21px solid transparent;\n          border-bottom: 22px solid transparent;\n          border-left: 12px solid $white;\n          top: -9px;\n          right: -23px;\n        }\n      }\n      span{\n        display: inline-block;\n        vertical-align: top;\n      }\n    }\n  }\n\n  &.bg-success,&.bg-dark,&.bg-danger,&.bg-warning,&.bg-primary,&.bg-info{\n    border: none;\n    .breadcrumb-item{\n      color: $white;\n      &:before{\n        color: inherit;\n      }\n      a,span{\n        color: inherit;\n      }\n    }\n  }\n}\n\n\n/* inverse breadcrumb */\n@each $color, $value in $theme-colors {\n  .bg-inverse-#{$color} {\n    @include breadcrumb-inverse-variant($value);\n  }\n}\n","// Color Functions Mixins\n\n// Solid tab variations\n@mixin tab-solid-variant($color) {\n  .nav-link {\n    &.active {\n      background: $color;\n    }\n  }\n}\n// Minimal tab variations\n@mixin tab-minimal-variant($color) {\n  .nav-tabs {\n    .nav-item {\n      .nav-link {\n        &.active {\n          color: $color;\n        }\n      }\n    }\n  }\n}\n// Sider color variations\n@mixin slider-color-variant($color) {\n  .noUi-base {\n    .noUi-connect {\n      background: $color;\n    }\n\n    .noUi-origin {\n      background: $color;\n\n      .noUi-handle {\n        &:after {\n          background: $color;\n        }\n\n        .noUi-tooltip {\n          background: $color;\n          color: $white;\n        }\n      }\n    }\n  }\n}\n// Breadcrumb color variations\n@mixin breadcrumb-inverse-variant($color) {\n  background: lighten($color, 25%);\n  border-color: $color;\n\n  .breadcrumb-item {\n    color: $color;\n\n    &:before {\n      color: inherit;\n    }\n\n    a {\n      color: inherit;\n    }\n  }\n\n  &.breadcrumb-custom {\n    background: transparent;\n\n    .breadcrumb-item {\n      background: lighten($color, 25%);\n\n      a {\n        color: $color;\n        border: none;\n\n        &:before {\n          border-left-color: lighten($color, 25%);\n        }\n      }\n\n      span {\n        color: $color;\n      }\n\n      &:last-child {\n        background: transparent;\n      }\n    }\n  }\n}\n// Pagination variations\n@mixin pagination-variants($color) {\n  .page-item {\n    &.active {\n      .page-link {\n        background: $color;\n        border-color: $color;\n      }\n    }\n\n    .page-link {\n      &:hover {\n        background: lighten($color,5%);\n        border-color: $color;\n        color: $white;\n      }\n    }\n  }\n}\n// Accordion Inverse variations\n@mixin accordion-inverse-variant($color) {\n  .card {\n    background: rgba($color, 0.16);\n    color: $color;\n    border: none;\n\n    .card-header {\n      color: $color;\n      border: none;\n\n      a,\n      h1,\n      h2,\n      h3,\n      h4,\n      h5,\n      h6,\n      p,\n      span {\n        color: $color !important;\n      }\n\n      a {\n        border-color: $color;\n\n        &:before {\n          color: $color !important;\n        }\n\n        &[aria-expanded=\"true\"] {\n          border-bottom: 1px solid rgba($color, 0.16);\n        }\n\n        &[aria-expanded=\"false\"] {\n          border-bottom: 1px solid transparent;\n        }\n      }\n    }\n  }\n}\n","/* Badges */\n\n.badge {\n  border-radius: .125rem;\n  font-size: 11px;\n  font-weight: initial;\n  line-height: 1;\n  padding: .375rem .5625rem;\n  font-family: $type1;\n  font-weight: $font-weight-medium;\n  &.badge-pill {\n    border-radius: 10rem;\n  }\n}\n\n\n/*Badge variations*/\n@each $color, $value in $theme-colors {\n  .badge-#{$color} {\n    @include badge-variations($value);\n  }\n}\n\n/*Badge outlined variations*/\n@each $color, $value in $theme-colors {\n  .badge-outline-#{$color} {\n    @include badge-outline-variations($value);\n  }\n}","//Badge Mixins\n\n//Badge vriations\n@mixin badge-variations($color) {\n\tborder: 1px solid $color;\n\tcolor: $white;\n}\n// Badge outlined variations\n@mixin badge-outline-variations($color) {\n\tcolor: $color;\n\tborder: 1px solid $color;\n}","/* Cards */\n\n.card {\n  border: 0;\n  border-radius: 0;\n  .card-body {\n    padding: $card-padding-y $card-padding-x;\n    + .card-body {\n      padding-top: 1rem;\n    }\n  }\n  .card-title {\n    margin-bottom: .75rem;\n    text-transform: capitalize;\n    font-family: $type1;\n    font-weight: $font-weight-medium;\n    font-size: 1.125rem;\n  }\n  .card-subtitle {\n    @extend .text-gray;\n    font-family: $type1;\n    margin-top: 0.625rem;\n    margin-bottom: 0.625rem;\n  \n  }\n  .card-description {\n    margin-bottom: 1.5rem;\n    font-family: $type1;\n    color: $card-description-color;\n  }\n  &.card-outline-success {\n\t\tborder: 1px solid theme-color(\"success\");\n\t}\n\t&.card-outline-primary {\n\t\tborder: 1px solid theme-color(\"primary\");\n\t}\n\t&.card-outline-warning {\n\t\tborder: 1px solid theme-color(\"warning\");\n\t}\n\t&.card-outline-danger {\n\t\tborder: 1px solid theme-color(\"danger\");\n\t}\n\t&.card-rounded {\n\t\t@include border-radius(5px);\n\t}\n\n  &.card-faded {\n    background: #b5b0b2;\n    border-color: #b5b0b2;\n  }\n  &.card-circle-progress {\n    color: $white;\n    text-align: center;\n  }\n  &.card-img-holder {\n    position: relative;\n    .card-img-absolute {\n      position: absolute;\n      top:0;\n      right: 0;\n      height: 100%;\n    }\n  }\n}\n\n@each $color, $value in $theme-colors {\n  .card-inverse-#{$color} {\n    @include card-inverse-variant(rgba(theme-color($color), .2), theme-color-level($color, 1), theme-color-level($color, 3));\n  }\n}\n","// Cards Mixins \n\n@mixin card-inverse-variant($bg, $border, $color) {\n  background: $bg;\n  border: 1px solid $border;\n  color: $color;\n}\n","/* Preview */\n\n.preview-list {\n  .preview-item {\n    @include display-flex;\n    @include flex-direction(row);\n    @include align-items(flex-start);\n    padding: .75rem 1.5rem;\n    font-size: